const AvailableMeals = () => {
  const [meals, setMeals] = useState([]); // initially empty array
  const [isLoading, setIsLoading] = useState(false);
  const [httpError, setHttpError] = useState(null);

  useEffect(() => {
    setIsLoading(true);
    const fetchMeals = async () => {
      // workaround async canno be put directly on useEffect
      const response = await fetch(
        'https://http-react-791ad-default-rtdb.europe-west1.firebasedatabase.app/meals.json',
        {}
      );

      if (!response.ok) {
        throw new Error('Something went wrong!');
      }

      const responseData = await response.json();
      console.log(responseData, response.ok);

      const loadedMeals = [];
      for (const key in responseData) {
        loadedMeals.push({
          id: key,
          name: responseData[key].name,
          description: responseData[key].description,
          price: responseData[key].price,
        });
      }
      setMeals(loadedMeals); // set meals state to loadedMeals
      setIsLoading(false);
    };

    fetchMeals().catch((error) => {
      setIsLoading(false);
      setHttpError(error.message);
    });
  }, []);

  if (isLoading) {
    return (
      <section className={`${classes.meals} ${classes.mealsloading}`}>
        <Card>
          <p>Loading...</p>
        </Card>
      </section>
    );
  }

  if (httpError) {
    return (
      <section className={`${classes.meals} ${classes.mealserror}`}>
        <Card>
          <p>{httpError}</p>
        </Card>
      </section>
    );
  }

  const mealsList = meals.map((meal) => (
    <MealItem
      id={meal.id}
      meal={meal}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card className='meals-list'>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default AvailableMeals;
